.App {
  background-color: #282c343a;
background-image: radial-gradient(#adbee0 0.5px, #282c34 0.5px);
background-size: 10px 10px;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
}

.title{
  font-size: 40px;
  text-align: center;
  margin-bottom: 40px;
}

form {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 10px;
}

input[type='text'] {
  padding-left: 20px;
  border: 2px solid #585d66;
  width: 60vw;
}

button {
  border: none;
  cursor: pointer;
}

.image-container {
  margin-top: 20px;
  border: 1px solid #ccc;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
  max-width: 400px;
  border-radius: 5px;
  margin-bottom: 10px;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
  border-radius: 5px;
}
